*{
  margin: 0;
  padding: 0;
}

.App{
  background-color: #16202A;
  color: #fff;
  .thrends{
    h1{
      padding: 0.3em;
      text-align: center;
    }
    .thrend_container{
      margin: 1em 0;
      border-bottom: 1px solid #8799A5;
      padding: 1em;
      .thrend_title{
        font-weight: bolder;
        margin: 0.5em 0;
        text-transform: uppercase;
      }
      .thrend_content{
        font-size: 0.9em;
        color: #8799A5;
      }
    }
    form{
      padding: 1em;
     input{
        width: 100%;
        display: block;
        margin: 0.3em 0;
        padding: 0.3em;
        font-size: 1.1em;
        outline: none;
        border: none;
        background-color: #8799A5;
        color: #fff;
        &::placeholder{
         // color: #fff;
        }
       
      }
      .content_input{
        height: 100px;
        width: 100%;
        outline: none;
        border: none;
        padding: 0.3em;
        margin: 0.5em 0;
        background-color: #8799A5;
        color: #fff;
        padding: 0.3em;
        font-size: 1.1em;
        &::placeholder{
      //    color: #fff;
        }
        
      }
      input[type=submit]{
        width: initial;
        margin: 0 auto;
        border-radius: 10px;
        background-color: #16202A;
        border: 3px solid #8799A5;
        color:#8799A5 ;
        font-size: 0.8em;
        &:hover{
          background-color: #8799A5;
          color:#16202A;
        }
      }
    }
  }
}